
import Group from 'vux/src/components/group/index.vue'
import Panel from 'vux/src/components/panel/index.vue'
import Cell from 'vux/src/components/cell/index.vue'
import Card from 'vux/src/components/card/index.vue'
import Box from 'vux/src/components/box/index.vue'
import CellFormPreview from 'vux/src/components/cell-form-preview/index.vue'
import XButton from 'vux/src/components/x-button/index.vue'


export default {
name: 'Home',
components: {
XButton,
Group,
Panel,
Cell,
Card,
Box,
CellFormPreview
},
mounted: function () {
const userOjb = JSON.parse(window.localStorage.getItem('User'))
this.user = userOjb
this.owner[0].title = this.user.nickName

},
data () {
return {
user: '',
owner: [{
src: require('../../assets/my/people.png'),
title: '',
desc: '',
url: '/my/resetSetting'
}]
}
},
methods: {
}
}



